import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Nav from './NavBar/Nav'
import Hero from './SectionHero/Hero'
import Posts from './FeaturedPosts/Featured'
import Post from './PostDetails/Post'
import Empty from './EmptyComponent/Empty'
import './App.css';


function App() {



  return (
    <div className='App'>
      <Router>
        <Nav />
        <Route path = '/task' exact component = {Hero} />
        <Route path = '/task' exact component = {Posts} />
        <Switch>
          <Route path = '/task/postdetails/:id' exact component = {Post} />
          <Route path = '/task/empty' exact component = {Empty} />
        </Switch>
      </Router>
    </div>
  )
}

export default App;
