import React from 'react';
import { Link } from 'react-router-dom';
import './Nav.css';


function Nav() {


  return ( 
    <div className='container'>
      <nav className="navbar navbar-expand-lg navbar-light ">
        <Link to='/task' style={{textDecoration:'none'}} ><p className="navbar-brand" >BLOGINIK</p></Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
 
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item active">
              <Link to='/task' style={{textDecoration:'none'}} ><p className="nav-link" >Home</p></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/empty' style={{textDecoration:'none'}} ><p className="nav-link" >About</p></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/empty' style={{textDecoration:'none'}} ><p className="nav-link" >Category</p></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/empty' style={{textDecoration:'none'}} ><p className="nav-link" >Pages</p></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/empty' style={{textDecoration:'none'}} ><p className="nav-link" >Layout</p></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/empty' style={{textDecoration:'none'}} ><p className="nav-link"p>Contact</p></Link>
            </li>

          </ul>
        </div>
      </nav>
    </div>
  )
}

export default Nav;
