import React, {useEffect, useState } from 'react';
import Single from '../Single/Single';
import { Link } from 'react-router-dom';
import axios from 'axios';


function Hero (){
 
  
  const [facts, setFacts] = useState({ facts: [], id : 12});
  useEffect(() => {
  axios.get( 'https://jsonplaceholder.typicode.com/posts').then(
  res =>{

    setFacts({fac : ''})
    console.log(res.data);
    console.log(facts);

  })}, []);
  

  return (
    <div className='container heroo-container'>
        <div className='row'>
          <div className=' item col-12'>
            <Single id = {facts.id} />
          </div>

          <div className='item col'>
            <Single />
          </div>
          <div className='item middle col-md-5 col-12'>
            <Single />
          </div>
          <div className='item col'>
            <Single />
          </div>

        </div>
    </div>
  )
}

export default Hero;
